<template>
	<div class="container">
		<div class="row align-items-center">
			<div class="col">
				<h1>{{title}}</h1>
				<br>
				<form @submit.prevent="login">
					<div class="form-group" >
						<label for="email">Email</label>
						<input type="email" class="form-control" v-model="email">
					</div>
					<div class="form-group">
						<label for="password">Password</label>
						<input type="password" class="form-control" v-model="password">
					</div>
					<div class="text-danger">
						<p v-if="erro">{{erro}}</p>
						<p v-else> 	&nbsp; </p>
					</div>
					<button type="submit" class="btn btn-primary">
						Login
					</button>
				</form>
			</div>
		</div>
	</div>
</template>


<script>
	import {signInWithEmail} from '../js/firebase'

	export default{
		name: 'login',
		data(){
			return {
				'title': 'Login',
				'erro': '',
				'email':'',
				'password': ''
			}
		},
		methods:{
			login(){
				this.erro = '';
				signInWithEmail(this.email, this.password, (erro) => {
					return this.erro = erro;
				})
			}
		}
	}
</script>

<style scoped>
	.container{
		max-width: 450px;
	}
	.row{
		height: 100vh;
		
	}
</style>


